.landing{
    position: relative;
    background-color: rgba(0, 255, 255, 0);
    width: 100%;
}
.frame{
    z-index: -1;
    position: absolute;
    top: -20%;
    left: 50%;
    width: 60%;
    height: 90%;
}
.landing-body{
    position: absolute;
    width: 100%;
    height: 100%;
}